<template>
  <div class="team container">
    <div class="team--info" v-if="teamInfo">
        <img :src="teamInfo.team.logo" alt="">
        <h1>{{teamInfo.team.name}}</h1>
    </div>
    <input type="text" placeholder="Search player" v-model="searchPlayer" @keyup="changeValue" @keydown="deleteTimer" class="team--search">
    <div class="tab">
        <h2 @click="activeTab='players'" :style="activeTab !== 'players' ? 'opacity: 0.5' : ''">Players</h2>
        <h2 @click="activeTab='matches'" :style="activeTab !== 'matches' ? 'opacity: 0.5' : ''">Matches</h2>
    </div>
    <div class="players" v-if="activeTab == 'players'">
        <div class="players--block players--header">
            <div class="player--photo">
                Photo
            </div>
            <div class="player--name">
                Name
            </div>
            <div class="player--height">
                Height/Weight
            </div>
            <div class="player--nationality">
                Nationality
            </div>
            <div class="player--age">
                Age
            </div>
            <div class="player--goals">
                Goals
            </div>
            <div class="player--assists">
                Assists
            </div>
            <div class="player--saves">
                Saves
            </div>
        </div>
        <div class="players--body">
            <div class="players--block player" v-for="(item, index) in players" :key="index">
                <div class="player--photo">
                    <img :src="item.player.photo" alt="">
                </div>
                <div class="player--name">
                    {{item.player.name}}
                </div>
                <div class="player--height">
                    {{item.player.height}}/{{item.player.weight}}
                </div>
                <div class="player--nationality">
                    {{item.player.nationality}}
                </div>
                <div class="player--age">
                    {{item.player.age}}
                </div>
                <div class="player--goals">
                    {{item.statistics[0].goals.total !== null ? item.statistics[0].goals.total : '-'}}
                </div>
                <div class="player--assists">
                    {{item.statistics[0].goals.assists !== null ? item.statistics[0].goals.assists : '-'}}
                </div>
                <div class="player--saves" v-if="item.statistics[0].goals.saves !== null">
                    {{item.statistics[0].goals.saves}}
                </div>
                <div class="player--saves" v-else>
                    -
                </div>
            </div>
        </div>
    </div>
    <div class="team--info-matches" v-if="teamFixtures && activeTab == 'matches'">
        <div class="match" v-for="(item, index) in teamFixtures" :key="index" @click="$router.push({path: `/match/${item.fixture.id}`})">
            <div class="home--match match--block">
                <img :src="item.teams.home.logo" alt="">
                {{item.teams.home.name}}
                <span>{{item.goals.home}}</span>
            </div>
            -
            <div class="away--match match--block">
                <p>{{item.goals.away}}</p>
                <img :src="item.teams.away.logo" alt="">
                {{item.teams.away.name}}
            </div>
        </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import {mapState} from 'vuex';
import {ref} from 'vue';

export default {
  name: 'TeamView',
    setup() {
        const activeTab = ref('players');

        const searchPlayer = ref('');
        const timer = ref(null);

        return {
            activeTab,
            searchPlayer,
            timer,
        }
    },
  computed: {
    ...mapState(['teamInfo', 'teamFixtures', 'players']),
  },
  methods: {
    changeValue(value) {
      clearTimeout(this.timer);
      let obj = {
        id: this.$route.params.id,
        search: this.searchPlayer !== '' ? this.searchPlayer : null
      }
      this.timer = setTimeout(() => {
        this.$store.dispatch('getPlayersInfo', obj);
      }, 1000)
    },
    deleteTimer() {
        clearTimeout(this.timer);
    },
  },
  mounted() {
    let obj = {
        league: this.$route.params.league,
        team: this.$route.params.id
    }
    this.$store.dispatch('getTeamInfo', obj)
    this.$store.dispatch('getFixturesByTeamId', this.$route.params.id)
    let objPlayer = {
        id: this.$route.params.id,
        search: null
    }
    this.$store.dispatch('getPlayersInfo', objPlayer)
  }
}
</script>

<style lang="scss">
    .tab {
        display: flex;
        align-items: center;
    }
  .team {
    display: flex;
    flex-direction: column;
    align-items: center;
    &--search {
        padding-top: 20px;
        padding-bottom: 20px;
        padding-left: 5px;
        padding-right: 5px;
        backdrop-filter: blur(60px);  
        border-radius: 10px;
        border: 1px solid #000;
        margin-bottom: 20px;
        &::placeholder {
            color: #fff;
        }      
    }
    h2 {
        font-size: 24px;
        margin-bottom: 20px;
        cursor: pointer;
        &:not(:last-child) {
            margin-right: 20px;
        }
    }
    &--info {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin-top: 30px;
        &-matches {
            display: flex;
            flex-direction: column;
            align-items: center;
            .match {
                display: flex;
                align-items: center;
                margin-bottom: 20px;
                backdrop-filter: blur(60px);
                border-radius: 20px;
                padding: 20px;
                cursor: pointer;
                img {
                    width: 20px;
                    height: 20px;
                    object-fit: contain;
                    margin-right: 8px;
                    margin-left: 8px;
                }
                &--block {
                    display: flex;
                    align-items: center;
                    margin-right: 10px;
                }
                span {
                    margin-left: 10px;
                }
                p {
                    margin-left: 10px;
                }
            }
        }
    }
    .players {
        width: 100%;
        &--header {
            border-bottom: 1px solid #000;
            padding: 20px;
            padding-bottom: 10px;
            margin-bottom: 20px;
            font-size: 20px;
            font-weight: 700;
        }
    }
    .players--block {
        display: grid;
        grid-template-areas: 'photo name height nation age goals assists saves';
        grid-template-columns: 20% 20% 20% 20% 5% 5% 5% 5%;
    }
    .player {
        margin-bottom: 20px;
        padding-bottom: 10px;
        backdrop-filter: blur(60px);
        padding: 20px;
        align-items: center;
        font-size: 20px;
    }
  }
</style>